.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.container {
     width: 95%;
     margin-left: auto;
     margin-right: auto;
}
.nav {
     background-color: #0082CA;
     color:#fff;
}
.navbar-brand {
     background-color: #0082CA;
     padding-left: 55px;
}
.card {
     margin: 20px auto;
}
.card-block {
     width: 90%;
     margin: 20px auto;
     text-align: left;
}
.card-block-form {
     width: 80%;
     margin: 20px auto;
}
.form-span {
     width: 250px;
}
.form-control {
     width: 125px;
}
.input-group {
     width: 100%;
}
.margin-all {
     margin: 25px;
}
.row {
     border: none;
}



